export default {
	data() {
		return {
			idx:"-1",
			list:[],
			priceArea:'',
			listItem:'',
			flag:false,
			
		};
	},
	methods:{
		// 选择类别
		select(index,item){
			this.idx = index;
			this.listItem = item;
			this.$emit('priceArea',this.idx,this.priceArea,item)
		},
		// 输入框失去焦点
		onBlur(){
			this.flag = false;
			this.$emit('priceArea',this.idx,this.priceArea,this.listItem);
			if(this.priceArea !== ''){
				let regex=/^[0]+/;
				this.priceArea=this.priceArea.replace(regex,'');
				if(this.priceArea == ''){
					this.priceArea = 0;
				}
				if(this.priceArea.length > 4 ){
					this.priceArea = this.priceArea.slice(0,4);
				}
				this.priceArea = this.priceArea + 'm²';
			}
		},
		// 输入框获得焦点
		onFocus(e){
			if(this.priceArea !== ''){
				this.priceArea = this.priceArea.slice(0,this.priceArea.indexOf('m²'));
			}
		},
		// 判断用户输入的内容只能输4位
		onInput(e){
			if(this.priceArea.length > 4){
				uni.showModal({
				    title: '输入的内容不能超过4位',
					showCancel:false
				});
				this.priceArea = this.priceArea.slice(0,4);
				this.flag = true;
			}
		}
	},
	mounted(){
		this.$emit('priceArea',this.idx,this.priceArea);
		let categorys;
		if (uni.getStorageSync("catalogues")) {
			categorys = uni.getStorageSync("catalogues").categorys;
		} else if (this.$store.state.catalogues) {
			categorys = this.$store.state.catalogues.categorys;
		}
		if (categorys && categorys.length > 0) {
			let list = categorys.filter((item)=>{
				return item.type == 1;
			})
			this.list = list;
			// this.list.pop();
		}
	}
}
